      </div>
    </div>
    <div class="form-group col-sm-12">
      <div class="col-sm-6">
        <label class="control-label col-sm-4">部门：</label>
        <div class="col-sm-8">
          <input class="form-control" type="text" v-model="model.f_payfee_department" disabled>
